@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
*,
*::before,
*::after {box-sizing: border-box;-webkit-tap-highlight-color: transparent}

:root {
  --border: 1px solid black;
  --border-radius: 100px;
}

p,h1,h2,h3,ul {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: 'Montserrat', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#container {
  width: min(1024px, 85%);
  margin: 0 auto;
}

:root {
    --clr-primary: hsl(270, 56%, 63%);
    --clr-primary-dark: hsl(270, 56%, 56%);
    --clr-primary-light: hsl(270, 56%, 83%);
    --clr-secondary: hsl(256, 41%, 47%);
    --clr-secondary-dark: hsl(256, 41%, 37%);
    --clr-light-grey: hsl(0, 0%, 93%);
    --clr-body: hsl(279, 42%, 85%);
}
:root {
    --fz-xs: .75rem;   /* 12px */
    --fz-s: .875rem;   /* 14px */
    --fz-m: 1rem;   /* 16px */
    --fz-l: 1.5rem;   /* 24px */
    --fz-xl: 2.25rem;   /* 36px */
    --fz-xxl: 3rem;   /* 48px */
    /* ////////////////////////// */
    --fw-light: 400;
    --fw-normal: 500;
    --fw-bold: 600;
    --fw-extra-bold: 700;
}
.navbar-header {
    width: 100%;
    background-color: #fff;
    z-index: 1000;
    position: -webkit-sticky;
    position: sticky;
    top: 0
}
.scroll-border {
    box-shadow: 0 -25px 32px #000;
}
.navbar nav ul, .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.logo {
    cursor: pointer;
    transition: .2s ease-in;
}
.logo a {text-decoration: none}
.logo img {max-width: 110px}
.logo:hover {
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
    transition: .25s ease-out
}
.navbar {height: 72px}
nav {display: flex}
.navbar nav ul {list-style: none}
.navbar nav > ul li + li {margin-left: 2rem}
.navbar nav > ul li a {
    font-weight: var(--fw-normal);
    cursor: pointer;
    transition: .15s ease-out;
    position: relative;
}
.navbar nav > ul li a.active-link,
.navbar nav > ul li a:hover {
    color: var(--clr-primary);
    transition: .15s ease-out;
}
.navbar nav > ul li a.active-link::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 4px;
    background-color: var(--clr-primary);
    bottom: -16px;
    border-radius: 100px;
    left: 50%;
    -webkit-animation: scaleHz .2s ease-out forwards;
            animation: scaleHz .2s ease-out forwards;
}
.nav-lang-change {margin-left: 2rem}
.nav-lang-change ul li + li {margin-left: .35rem}
.nav-lang-change ul li {
    font-weight: var(--fw-bold);
    cursor: pointer;
    padding: .5em;
    border-radius: 50%;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    transition: .2s ease-out;
}
.nav-lang-change ul li p {font-size: var(--fz-s)}
.nav-lang-change ul li:not(.selected-lang):hover {
    background-color: rgb(245, 245, 245);
    transition: .2s background-color ease-out
}
.nav-lang-change ul li.selected-lang {
    background-color: var(--clr-primary);
    color: #fff;
}
.nav-icon {display: none}

@media only screen and (max-width: 850px) {
    .nav-icon {display: block}
    .navbar nav > ul {display: none}
    .navbar nav > ul.hidden li a.active-link::before{display: none}
    .navbar nav > ul.hidden {
        display: block;
        position: absolute;
        background-color: var(--clr-primary-light);
        left: 0;
        top: 72px;
        width: 100%;
        text-align: center;
        -webkit-animation: slideDown ease-out .2s forwards;
                animation: slideDown ease-out .2s forwards;
    }
    .navbar nav > ul.hidden li {padding: 1em}
    .navbar nav > ul.hidden li + li {margin: 0}
    .navbar nav > ul.hidden li:hover {background-color: var(--clr-primary)}
    .navbar nav > ul.hidden li a.active-link,
    .navbar nav > ul.hidden li a:hover {color: black}
    .logo img {max-width: 100px}
    .navbar nav > svg {
        margin-left: 1rem;
        cursor: pointer;
    }
}

@-webkit-keyframes scaleHz {
    from {-webkit-transform: scaleX(0) translateX(-50%);transform: scaleX(0) translateX(-50%)}
    to {-webkit-transform: scaleX(1) translateX(-50%);transform: scaleX(1) translateX(-50%)}
}

@keyframes scaleHz {
    from {-webkit-transform: scaleX(0) translateX(-50%);transform: scaleX(0) translateX(-50%)}
    to {-webkit-transform: scaleX(1) translateX(-50%);transform: scaleX(1) translateX(-50%)}
}
@-webkit-keyframes slideDown {
    from {-webkit-transform: scaleY(0);transform: scaleY(0);
    -webkit-transform-origin: top;
            transform-origin: top;}
    to {-webkit-transform: scaleY(1);transform: scaleY(1);
        -webkit-transform-origin: top;
                transform-origin: top;}
}
@keyframes slideDown {
    from {-webkit-transform: scaleY(0);transform: scaleY(0);
    -webkit-transform-origin: top;
            transform-origin: top;}
    to {-webkit-transform: scaleY(1);transform: scaleY(1);
        -webkit-transform-origin: top;
                transform-origin: top;}
}

.btn {
    text-decoration: none;
    padding: 1em 1.5em;
    border-radius: var(--border-radius);
    font-weight: var(--fw-bold);
    font-family: 'Montserrat' !important;
    transition: .15s ease-out;
    text-align: center;
    outline: none;
    cursor: pointer;
}
.project-details__image-container {
    width: 100%;
    height: calc(100vh - 73px);
    position: relative;
}
.project-details__image-container::before {
    content: '';
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    background: rgb(31, 31, 31);
    background: linear-gradient(180deg, rgba(31, 31, 31, 0.6) 0%, rgb(0, 0, 0) 130%);
}
.project-details__infos {
    position: absolute;
    z-index: 999;
    color: #fff;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.project-details__infos h1 {
    font-size: var(--fz-xxl);
    font-weight: var(--fw-extra-bold);
}
.project-details__infos p {
    margin: 1.5rem 0 2.8rem;
    max-width: 45ch;
    line-height: 1.6;
}
.project-details__infos svg {
    font-size: var(--fz-l);
    margin-bottom: 1rem;
    transition: .2s ease-out;
    cursor: pointer;
}
.project-details__infos svg:hover {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    transition: .3s ease-out
}

@media only screen and (max-width: 850px) {
    .project-details__image-container {background-position: center}
    .project-details__image-container::before{background: linear-gradient(180deg, rgba(0, 0, 0, 0.6) 0%, rgb(0, 0, 0) 100%);}
}
.scroll-to-top-icon {
    width: 3.2rem;
    height: 3.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    position: fixed;
    right: 1.5rem;
    bottom: 1.5rem;
    font-size: 1.8rem;
    border-radius: 50px;
    cursor: pointer;
    z-index: 10000;
    background-color: var(--clr-primary);
    transition: .2s ease-out;
    -webkit-animation: scrollToTop .2s ease-out;
            animation: scrollToTop .2s ease-out;
}
.scroll-to-top-icon:hover {
    background-color: var(--clr-primary-dark);
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
    transition: .2s ease-out
}
@-webkit-keyframes scrollToTop {
    from{-webkit-transform: scale(0);transform: scale(0)}
    to{-webkit-transform: scale(1);transform: scale(1)}
}
@keyframes scrollToTop {
    from{-webkit-transform: scale(0);transform: scale(0)}
    to{-webkit-transform: scale(1);transform: scale(1)}
}

@media only screen and (min-width: 850px) {
    .scroll-to-top-icon {
        width: 3.5rem;
        height: 3.5rem;
        right: 3rem;
        bottom: 2rem;
        font-size: 2rem
    }
}
.about-us-section {
    background-color: var(--clr-primary-light);
    padding: 2em 0 4em;
}
.about-us h1 {
    font-weight: var(--fw-light);
    margin: 2rem 0;
    text-align: center;
    position: relative;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
.about-us h1::before {
    content: '';
    position: absolute;
    left: 0;
    bottom: -8px;
    width: 100%;
    height: 5px;
    background-color: var(--clr-primary);
}
.about-us p {
    line-height: 1.6;
}
.input {
    position: relative;
    margin-bottom: 1.5rem
}

.input input, .input textarea {
    width: 100%;
    padding: 1em 1em 1em 1.5em;
    border: 2px solid var(--clr-primary-light);
    border-radius: 100px;
    outline: none;
    font-size: var(--fz-m)
}
.error-text {
    font-size: var(--fz-s);
    position: absolute;
    left: 20px;
    top: 3.5rem
}
.message-sent {top: 6.2rem !important}
.message-sent-icon {margin-right: .5rem}
.input textarea {
    resize: none;
    font-family: 'Montserrat';
    border-radius: 25px !important
}
.disabled {
    background-color: var(--clr-primary-light) !important;
    cursor: not-allowed !important;
}
.active-submit {
    width: 100%;
    padding: 1rem;
    border: none;
    font-size: var(--button-fz);
    border-radius: 100px;
    background-color: var(--clr-primary);
    cursor: pointer;
    color: #fff;
    transition: .15s ease-out;
}
.active-submit:hover {
    box-shadow: 0 8px 20px 0 var(--clr-primary-light);
    transition: .15s ease-out
}
.contact-us {
    padding: 4em 0 0;
    width: min(450px, 85%);
    margin: 0 auto;
}
.contact-us h1 {
    font-weight: var(--fw-light);
    margin: 0 0 3rem;
    text-align: center;
}

@media only screen and (max-width: 850px) {
    .contact-us{margin: 0 auto 1.5rem}
}
footer {
    background-color: var(--clr-primary);
    margin-top: -.5rem;
    padding-bottom: 1em;
    text-align: center;
    color: #fff;
}
footer p {
    font-size: var(--fz-s);
    font-weight: var(--fw-normal);
}
.hero {
    width: 100%;
    height: calc(100vh - 74px);
}
.hero-split {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}
.hero-split__right img {
    width: min(600px, 100%);
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.hero-split__right {flex-basis: 45%}
.hero-split__left {
    margin-right: 2rem;
    flex-basis: 55%;
    position: relative;
}
.hero-split__left .hero__heading h1 {
    font-size: var(--fz-xxl);
    font-weight: var(--fw-extra-bold);
    max-width: 16ch;
}
.hero-split__left .hero__heading p {
    line-height: 1.6;
    margin: 1rem 0 2.2rem;
    max-width: 45ch;
}
.hero-split__left::before {
    content: '';
    position: absolute;
    top: 0;
    left: -50px;
    width: 10px;
    height: 110%;
    background-color: var(--clr-primary);
    -webkit-animation: ColorRainbow 5s ease-out infinite;
            animation: ColorRainbow 5s ease-out infinite;
}

@media only screen and (max-width: 850px) {
    .hero{
        height: auto;
        margin: 2rem 0 3rem
    }
    .hero-split{
        flex-direction: column-reverse;
    }
    .hero-split__left {margin: 1rem 0 0 0}
    .hero-split__left::before {display: none}
    .hero-split__left .hero__heading h1 {
        font-size: var(--fz-xl);
        max-width: 20ch;
    }
    /* .hero-split__right{display: none;} */
}

@-webkit-keyframes ColorRainbow {
    0% {
        background-color: var(--clr-primary);
    }
    12.5% {
        background-color: var(--clr-primary-dark);
    }
    25% {
        background-color: var(--clr-secondary);
    }
    37.5% {
        background-color: var(--clr-secondary-dark);
    }
    50% {
        background-color: var(--clr-secondary-dark);
    }
    62.5% {
        background-color: var(--clr-secondary);
    }
    75% {
        background-color: var(--clr-secondary);
    }
    87.5% {
        background-color: var(--clr-primary-dark);
    }
    100% {
        background-color: var(--clr-primary);
    }
}

@keyframes ColorRainbow {
    0% {
        background-color: var(--clr-primary);
    }
    12.5% {
        background-color: var(--clr-primary-dark);
    }
    25% {
        background-color: var(--clr-secondary);
    }
    37.5% {
        background-color: var(--clr-secondary-dark);
    }
    50% {
        background-color: var(--clr-secondary-dark);
    }
    62.5% {
        background-color: var(--clr-secondary);
    }
    75% {
        background-color: var(--clr-secondary);
    }
    87.5% {
        background-color: var(--clr-primary-dark);
    }
    100% {
        background-color: var(--clr-primary);
    }
}
.technologies {
    width: 100%;
    padding: 2em 0 5em;
}
.technologies h1 {
    text-align: center;
    margin: 3em 0 4em;
    font-weight: var(--fw-light);
}
.techs {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.techs-col {
    flex-basis: 100%;
    display: flex;
    justify-content: center;
    position: relative;
}
.techs-col > svg {
    transition: .2s ease-out;
    font-size: 60px !important;
}
.techs-col > svg:hover {-webkit-transform: scale(1.09);transform: scale(1.09)}
.techs-col .tech-name {
    position: absolute;
    top: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    -webkit-animation: PopUp .3s ease-out forwards;
            animation: PopUp .3s ease-out forwards;
}

@media only screen and (max-width: 850px) {
    .techs-col > svg {font-size: 45px !important}
    .technologies h1 {
        max-width: 18ch;
        margin: 2em auto 3em;
    }
}

@-webkit-keyframes PopUp {
    from {
        -webkit-transform: translate(-50%, 0) scale(0);
                transform: translate(-50%, 0) scale(0);
        opacity: 0;
    }
    to {
        -webkit-transform: translate(-50%, -40px) scale(1);
                transform: translate(-50%, -40px) scale(1);
        opacity: 1;
    } 
}

@keyframes PopUp {
    from {
        -webkit-transform: translate(-50%, 0) scale(0);
                transform: translate(-50%, 0) scale(0);
        opacity: 0;
    }
    to {
        -webkit-transform: translate(-50%, -40px) scale(1);
                transform: translate(-50%, -40px) scale(1);
        opacity: 1;
    } 
}

.works,
.works > * > a {
    display: flex;
    align-items: center;
    justify-content: center;
}
.works {max-width: 100%}
.works > * > a img {
    max-width: 100%;
    cursor: pointer;
    transition: .15s ease-out;
}
.works > * {
    flex-basis: 80%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    overflow: hidden;
}
.works > * > a img:hover {
    -webkit-transform: scale(1.09);
            transform: scale(1.09);
    transition: .3s ease-out;
}
.darken-image {-webkit-filter: brightness(.35);filter: brightness(.35)}

@media only screen and (max-width: 850px) {
    .works {flex-direction: column}
    .darken-image {-webkit-filter: brightness(1);filter: brightness(1)}
}
